.slider-container{
    display: inline-block;
    position: relative;
    background-color: transparent;
    height: 15vW;
    margin: 35% auto 0 auto;
}
.slider-viewport{
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    height: inherit;
    /* flex-grow: 1; */
    z-index: 1;
    position: relative;
}
.slider-viewport::-webkit-scrollbar{
    /* ELIMINATES the scrollbar, DEPRECATED on firefox, check alternative method */
    display: none;
}
.slider-nav{
    /* REMEMBER: delete button color; */
    /* Using flex-basis instead of width */
    /* flex-basis: 20px;
    flex-shrink: 0; */
    position: absolute;
    top: 0;
    bottom: 0;
    width: 3%;
    cursor: pointer;
    background-color: transparent;
    padding: 0;
    outline: none;
    opacity: 0;
    border: none;
    -webkit-transition: opacity .2s ease-in-out;
    -o-transition: opacity .2s ease-in-out;
    transition: opacity .2s ease-in-out;
    z-index: 3;

}
.slider-nav:focus {
    background-color: rgb(27, 27, 25);
}
.slider-nav:hover {
    opacity: 0.8;
    border:  1px solid rgb(41, 39, 39);
    background-color: rgb(27, 27, 25);
}
.slider-left-nav{
    opacity: 0.3;
    background-color: rgb(27, 27, 25);
    left: 0;
}
.slider-right-nav{
    opacity: 0.3;
    background-color: rgb(27, 27, 25);
    right: 0;
}
.slider-container .slider-nav-disable{
    opacity: 0;
    z-index: 0;
    cursor: default;
    -webkit-user-select:  none;
       -moz-user-select:  none;
        -ms-user-select:  none;
            user-select:  none;
    pointer-events: none;
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
}


