.catalog-container{
    width: 100%;
    height: 100%;
}

.main-catalog {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 54vh;
    background-color: #000e13;
}

.div-block {
    /* position: relative; */
    /* top: 54vh; */
    height: 40vh;
    left: 0px;
    right: 0px;
    bottom: 0px;
    display: -ms-flexbox;
    display: flex;
    margin-left: 50px;
    width: 100%;
}

.filter-search-container{
    width: 100%;
    height: 30vh;
}

.catalog-buttons-container{
    width: 100%;
    height: 10vh;
}
  
.catalog-bottom {
    display: -ms-flexbox;
    display: flex;
    height: 40vh;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex: 1;
    flex: 1 1;
}
  
.catalog-bottom-top {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 10vh;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex: 1;
    flex: 1 1;
    background-color: #072032;
}

.catalog-bottom-title {
    display: -ms-flexbox;
    display: flex;
    width: 50%;
    height: 100%;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}

.catalog-bottom-title-text {
    display: -ms-flexbox;
    display: flex;
    width: 50%;
    height: 100%;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    font-family: Exo, sans-serif;
    color: #fff;
    font-size: 24px;
    line-height: 24px;
    font-style: normal;
}

.catalog-bottom-content {
    width: 100%;
    height: 30vh;
    background-color: #072032;
}

.catalog-search {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 6vh;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}

.form-block-catalog {
    width: 50%;
    height: 100%;
    margin-bottom: 0px;
}

.w-form {
    margin: 0 0 15px;
}

.form-catalog {
    display: -ms-flexbox;
    display: flex;
    padding: 10px 20px 10px 20px;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-shadow: inset 0 0 8px 0 #000;
            box-shadow: inset 0 0 8px 0 #000;
}
.catalog-form-input {
    margin-bottom: 0px;
    margin-left: 2%;
    border: 0px solid #000;
    background-color: #072032;
    padding-left: 20px;
    color: #fff;
    font-size: 16px;
    width: 50%;
}

.w-form-done {
    display: none;
    padding: 20px;
    text-align: center;
    background-color: #dddddd;
}

.w-form-fail {
    display: none;
    margin-top: 10px;
    padding: 10px;
    background-color: #ffdede;
}

.catalog-filter {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 14vh;
    padding-top: 10px;
    padding-right: 20px;
    padding-left: 20px;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -ms-flex-align: center;
    align-items: center;
}
  
.catalog-filter-menu {
    display: -ms-flexbox;
    display: flex;
    width: 100px;
    height: 40px;
    margin-right: 20px;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    -ms-flex-align: center;
    align-items: center;
    border-radius: 5px;
    background-color: #1f5566;
    font-family: Exo, sans-serif;
    color: #fff;
}
  
.catalog-filter-menu:hover {
    background-color: #027191;
}
  
.catalog-filter-property {
    display: -ms-flexbox;
    display: flex;
    margin-right: 20px;
    padding: 5px 12px;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    -ms-flex-align: center;
    align-items: center;
    border: 2px solid #1f5566;
    border-radius: 5px;
    background-color: #fff;
    color: #1f5566;
}
  
.catalog-filter-property:hover {
    background-color: #d3d3d3;
}
  
.catalog-property-icon {
    margin-right: 5px;
}

.w-inline-block {
    max-width: 100%;
    display: inline-block;
}

.catalog-buttons {
    position: relative;
    right: 0px;
    bottom: 0px;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 10vh;
    -ms-flex-align: center;
    align-items: center;
    color: #fff;
}
  
.catalog-button {
    display: -ms-flexbox;
    display: flex;
    width: 20%;
    height: 100%;
    -ms-flex-align: center;
    align-items: center;
    background-color: #1f5566;
}
  
.catalog-button:hover {
    background-color: #027191;
}
  
.catalog-button-icon {
    display: -ms-flexbox;
    display: flex;
    width: 30%;
    height: 100%;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}
  
.catalog-button-text {
    display: -ms-flexbox;
    display: flex;
    width: 70%;
    height: 100%;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    font-family: Exo, sans-serif;
    font-size: 16px;
    text-align: center;
}
  
.catalog-vertical-bar {
    width: 1px;
    height: 80%;
    background-color: hsla(0, 0%, 100%, .2);
}
  
  


